<template>
  <div>
    <title-component v-on:message="showEdit"></title-component>
    <div class="container">
      <router-view></router-view>
    </div>
    <div>
       <edit-Vuedal></edit-Vuedal>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { default as Vuedals, Component as Vuedal, Bus } from 'vuedals'
import title from '../components/layouts/title'
import qcteate from '../components/questions/q_create'

Vue.use(Vuedals)
export default {
  name: 'home',
  data: function () {
    return {
      name: '张三',
      islogin: false
    }
  },
  methods: {
    showEdit: function () {
      Bus.$emit('new', {
        title: '提问',
        name: 'edit-div',
        size: 'sm',
        component: qcteate
      })
    }
  },
  components: {
    'title-component': title,
    'edit-Vuedal': Vuedal
  }
}
</script>

<style>

</style>




