<template>
  <div class="edit-div-div">
    <form>
      <div class="form-group">
        <input type="text" class="form-control" aria-label="...">
      </div>
      <div class="form-group">
        <div style="line-height:21px">
          <label>问题说明</label>
          <!--<span class="caret" style="float:right;margin-top:10px;margin-right:10px" @click="showToolbar"></span>-->
        </div>
        <quill-editor class="editor-example" ref="myTextEditor" :content="content" :options="editorOption" @change="onEditorChange($event)">
        </quill-editor>
      </div>
      <div class="form-group">
        <label>选择话题</label>
        <input type="text" class="form-control" aria-label="...">
      </div>
      <div class="form-group">
        <div style="float:right">
          <a class="btn btn-link ">
            取消
          </a>
          <button type="submit" class="btn btn-primary">
            发布
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { quillEditor } from 'vue-quill-editor'
import { } from 'quill/themes/snow'

var editconfig = {
  theme: '',
  formula: true,
  modules: {
    toolbar: [
      [{ 'size': ['small', false, 'large'] }],
      ['bold', 'italic'],
      [{ 'list': 'ordered' }, { 'list': 'bullet' }],
      ['link', 'image'],
      ['clean']
    ],
    history: {
      delay: 1000,
      maxStack: 50,
      userOnly: false
    }
  }
}

export default {
  name: 'qcteate',
  data: function () {
    return {
      isShow: false,
      content: '',
      editorOption: editconfig
    }
  },
  components: {
    quillEditor
  },
  computed: {
    editor: function () {
      return this.$refs.myTextEditor.quillEditor
    }
  },
  methods: {
    onEditorChange: function ({ editor, html, text }) {
      console.log('editor change!', editor, html, text)
      this.content = html
    },
    showToolbar: function () {
      this.isShow = !this.isShow
      if (this.isShow) {
        console.log('1111111111111111')
        this.changeInfo('')
      } else {
        console.log('2222222222222222')
        this.changeInfo('bubble')
      }
    }
  }
}
</script>

<style>
.edit-div {
  padding: 0px;
  margin-top: 8em;
  border-radius: 8px;
}

.vuedal header {
  min-height: 35px;
  background: #0d73da;
  background: -webkit-linear-gradient(top, #0e7bef, #0d73da);
  background: linear-gradient(to bottom, #0e7bef, #0d73da);
  margin-bottom: 10px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}

.vuedal header .title {
  margin-top: 5px;
  margin-left: 12px;
  float: left;
  font-size: 13px;
  color: #fff;
}

.vuedal header .close {
  margin-top: 5px;
  margin-right: 10px;
  color: #fff;
}

.edit-div-div {
  min-height: 410px;
  padding: 15px;
  /*font-family: 'Avenir', Helvetica, Arial, sans-serif;*/
}

.editor-example {
  border: 1px solid #ccc;
}

.ql-container .ql-editor {
  min-height: 10em;
  padding-bottom: 1em;
}

.ql-toolbar .ql-formats {
  float: left;
}
</style>



