<template>
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container">
      <div class="navbar-header">
        <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
          <span class="sr-only">Toggle navigation</span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </button>
        <a class="navbar-brand">知乎</a>
      </div>

      <div id="navbar" class="navbar-collapse collapse">
        <ul class="nav navbar-nav">
          &nbsp;
        </ul>
        <div class="navbar-left">
          <form class="navbar-form top-search-form">
            <div class="input-group top-search-div">
              <input type="text" class="form-control" placeholder="搜索你感兴趣的话题">
              <div class="input-group-btn">
                <button class="btn btn-default" type="button">Go!</button>
              </div>
            </div>
          </form>
        </div>

        <!-- Right Side Of Navbar -->
        <div class="navbar-right">
          <button class="top-add-question" @click="showEdit">提问</button>
          <div class="top-nav-profile">
            <dropdown type="">
              <a slot="button">{{name}}</a>
              <li>
                <a href="#dropdown">Action</a>
              </li>
              <li>
                <a href="#dropdown">Another action</a>
              </li>
              <li>
                <a href="#dropdown">Something else here</a>
              </li>
              <li role="separator" class="divider"></li>
              <li>
                <a @click="logout">退出</a>
              </li>
            </dropdown>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import auth from '../service/auth'
import { dropdown } from 'vue-strap'
export default {
  name: 'title',
  data: function () {
    return {
      name: '张三',
      zoomModal: false
    }
  },
  methods: {
    logout: function () {
      var router = this.$router
      auth.logout()
      router.push('/')
    },
    showEdit: function () {
      this.$emit('message')
    }
  },
  components: {
    dropdown
  }
}
</script>

<style>
.navbar-default {
  background: #0767c8;
}

.navbar-default .navbar-brand {
  color: #f2f4f5;
  float: left;
  line-height: 30px;
  height: 100%;
  /*background-image: url(/static/revved/img/sticky_header/new_logo.ede2316d.png);*/
  background-repeat: no-repeat;
  background-position: 0 8px;
  /*text-indent: -9999px;*/
  font-size: 25px;
}

.navbar-default .navbar-nav>li>a {
  line-height: 30px;
  text-align: center;
  display: block;
  color: #f2f4f5;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .12);
  font-size: 16px;
}

.top-search-form {
  display: block;
  margin-top: 1em;
  line-height: 30px;
  display: block;
  position: relative;
  width: 400px;
}

.top-search-div {
  width: 100%;
  display: block;
}

.top-nav-profile {
  float: right;
  margin-top: 1em;
  margin-left: 50px;
  position: relative;
  min-width: 150px;
  height: 45px;
  z-index: 10;
  color: #fff;
}



.top-nav-profile .btn-group {
  float: left;
  width: 100%;
  height: 100%;
}

.top-nav-profile button {
  display: block;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .12);
  font-size: 16px;
  color: #fff;
  text-decoration: none;
  border-top: 0;
  background: transparent;
  box-shadow: none;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.top-nav-profile button>a {
  /*display: block;*/
  text-shadow: 0 1px 1px rgba(0, 0, 0, .12);
  font-size: 16px;
  color: #fff;
}

.top-add-question {
  width: 66px;
  height: 32px;
  line-height: 30px;
  margin-top: 1em;
  border: 1px solid #045bb2;
  border-radius: 4px;
  background: #0e78e7;
  background: -webkit-linear-gradient(top, #0e7bef, #0d73da);
  background: linear-gradient(to bottom, #0e7bef, #0d73da);
  box-shadow: 0 1px 2px rgba(0, 0, 0, .05), inset 0 1px 1px rgba(255, 255, 255, .15);
  color: #fff;
  font-size: 14px;
  text-align: center;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .12);
  outline: 0;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
</style>




