<template>
  <div class="container">
    <div class="row">
      <div class="index-main">
        <div class="index-main-body">
          <div class="index-header">
            <h1 class="title">知乎</h1>
            <h2 class="subtitle">与世界分享你的知识、经验和见解</h2>
          </div>
          <div class="index-tab-navs">
            <ul class="nav nav-tabs nav-justified">
              <li role="presentation" v-bind:class="{ active: 1===isActive }">
                <a href="#" v-on:click="select(1)">登陆</a>
              </li>
              <li role="presentation" v-bind:class="{ active: 2===isActive }">
                <a href="#" v-on:click="select(2)">注册</a>
              </li>
            </ul>
          </div>
          <div v-if="1===isActive">
            <form class="form-horizontal form-signin" role="form" @submit.prevent="signin">
              <div class="form-group  group-inputs">
                <input id="email" v-model="user.email" type="email" class="form-control" name="email" placeholder="邮箱" required autofocus>
                <input id="password" v-model="user.password" type="password" class="form-control" name="password" placeholder="密码" required>
              </div>
              <div class="form-group">
                <div>
                  <button type="submit" class="btn btn-primary btn-block btn-lg">
                    登陆
                  </button>
                  <a class="btn btn-link ">
                    忘记密码?
                  </a>
                </div>
              </div>
            </form>
          </div>
          <div v-else>
            <form class="form-horizontal form-signin" role="form" @submit.prevent="signup">
              <div class="form-group group-inputs">
                <input id="username" type="text" class="form-control" name="username" placeholder="用户名" required autofocus>
                <input id="email" type="email" class="form-control" name="email" placeholder="邮箱" required>
                <input id="password" type="password" class="form-control" name="password" placeholder="密码" required>
                <input id="password-confirm" type="password" class="form-control" name="password_confirmation" placeholder="确认密码" required>
              </div>
              <div class="form-group">
                <div class="">
                  <button type="submit" class="btn btn-primary btn-block btn-lg">
                    注册
                  </button>
                </div>
              </div>
            </form>
          </div>
          <alert-component v-model="showErr" placement="top" duration="3000" type="danger" width="400px" dismissable>
            <span class="icon-info-circled alert-icon-float-left"></span>
            <strong>注意!</strong>
            <p>{{errMsg}}</p>
          </alert-component>
        </div>
      </div>
      <div class="footer">
        <span>&copy; 2017 知乎</span>
        <span class="dot">·</span>
        <a href="/contact" class="footer-mobile-show">联系我们</a>
        <span class="dot">·</span>
      </div>
    </div>
  </div>
</template>

<script>
import auth from './service/auth'
import { alert } from 'vue-strap'

export default {
  name: 'main',
  data: function () {
    return {
      isActive: 1,
      showErr: false,
      errMsg: '',
      user: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    select: function (msg) {
      this.isActive = msg
    },
    signin: function () {
      var router = this.$router
      auth.login(this.user.email, this.user.password, function (res) {
        console.log(res)
        if (!res.ok) {
          alert(res.msg)
          // this.showErr = true
          // this.errMsg = loggedIn.msg
        } else {
          router.push('/home')
        }
      })
    }
  },
  components: {
    'alert-component': alert
  }
}
</script>



