import Vue from 'vue'

export default {
  login: function (email, pass, cb) {
    cb = arguments[arguments.length - 1]
    if (localStorage.token) {
      if (cb) cb(true)
      return
    }
    Vue.http.post('api/platform/login/login', { email: email, password: pass },
      { emulateJSON: true }).then((resp) => {
        var result = resp.data
        return result
      }, (resp) => {
        console.log(resp)
        return { ok: false, msg: '远程服务器异常' }
      }).then((res) => {
        if (res.ok) {
          if (cb) cb({ ok: true, msg: '' })
          localStorage.token = res.data
        } else {
          if (cb) cb({ ok: false, msg: res.msg })
        }
      })
  },
  getToken: function () {
    return localStorage.token
  },
  loggedIn: function () {
    return !!localStorage.token
  },
  logout: function (cb) {
    delete localStorage.token
    Vue.http.get('api/platform/login/logout')
    if (cb) cb()
  }
}



